@media screen and (min-width:240px) {
html, body, button, input, select, textarea {
    font-size: 9px;
}
}
@media screen and (min-width:320px) {
html, body, button, input, select, textarea {
    font-size: 12px;
}
}
@media screen and (min-width:375px) {
html, body, button, input, select, textarea {
    font-size: 14px;
}
}
@media screen and (min-width:420px) {
html, body, button, input, select, textarea {
    font-size: 16px;
}
}
/* 低版本浏览器出现滚动条，导致宽度小于480的处理*/
@media screen and (min-width:450px) {
html, body, button, input, select, textarea {
    font-size: 17px;
}
}
/* 1.5倍像素大小页面兼容代码 */
@media screen and (min-width:480px) {
html, body, button, input, select, textarea {
    font-size: 18px;
}
}
/* 针对小米的540分辨率做了优化 */
@media screen and (min-width:540px) {
html, body, button, input, select, textarea {
    font-size: 20.25px;
}
}
/* 针对魅族的600分辨率做了优化 */
@media screen and (min-width:600px) {
html, body, button, input, select, textarea {
    font-size: 22px;
}
}
/* 两倍像素大小页面兼容代码 */
@media screen and (min-width:640px) {
html, body, button, input, select, textarea {
    font-size: 24px;
}
}
/* 750分辨率 */
@media screen and (min-width:750px) {
html, body, button, input, select, textarea {
    font-size: 28.125px;
}
}
html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}
body {
  line-height: 1.6;
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
* {
  margin: 0;
  padding: 0;
}
a img {
  border: 0;
}
a {
  text-decoration: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}
/** env = windows **/
::-webkit-input-placeholder {
  font-family: -apple-system-font, "Helvetica Neue", sans-serif;
}
/** prevent default menu callout **/
a {
  -webkit-touch-callout: none;
}
html,
body,
#app {
  width: 100%;
  height: 100%;
}
body {
  background-color: #fff;
  font-family: ZpixEX2;
}
#app > div {
  height: 100%;
  box-sizing: border-box;
  position: relative;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}
button,
input {
  border: 0;
}
.p-img-container {
  overflow: hidden;
}
.p-img-container img {
  display: block;
  width: 100%;
}
.p-no-data {
  text-align: center;
  margin: 0.35714286rem auto;
  color: #7a7a7a;
}
.weui-loading_toast.vux-loading {
  position: relative;
  z-index: 9999;
}

.p-post-detail {
  padding: 1.28571429rem 0.71428571rem;
  color: #000;
}
.p-post-detail .p-post-header {
  padding: 0 0.64285714rem 1.14285714rem;
  border-bottom: 1px solid #f2f2f2;
}
.p-post-detail .p-post-title {
  font-size: 1.28571429rem;
  line-height: 1.85714286rem;
  color: #000;
}
.p-post-detail .p-post-from {
  margin-top: 0.85714286rem;
  font-size: 0.85714286rem;
  line-height: 1;
}
.p-post-detail .p-post-time {
  margin-top: 0.71428571rem;
  font-size: 0.85714286rem;
  line-height: 1;
  color: #687b90;
}
.p-post-detail .p-post-content {
  padding: 1rem 0.28571429rem;
  font-size: 1.07142857rem;
  line-height: 1.71428571rem;
}
.p-post-detail .p-post-content img {
  max-width: 100%!important;
  margin: 1.14285714rem auto;
}
